.page-circulation {
  .page-sub-title {
    @screen lg {
      @apply text-px44 leading-px54 tracking-em_001;
    }

    @screen xl {
      @apply text-px46 leading-px56;
    }
  }

  &__cards {
    @apply w-full tw-grid;
    row-gap: #{rem-calc(40)};

    @screen md {
      row-gap: #{rem-calc(30)};
    }

    .circulation-card {
      @apply col-span-full;

      @screen md {
        @apply col-span-6 col-start-2;
      }

      @screen lg {
        @apply col-span-6 col-start-auto;
      }
    }
  }
}
